body {
  font-size: 20px;
  font-weight: 700;
  width: 100vw;
  height: 100vh;
}

.app{
  width: 100%;
  height: 100vh;
  background: linear-gradient(145deg, rgb(10, 10, 10), rgb(37, 36, 36));
  display: grid;
  place-items: center;
}

.navIcon{
  width: 60px;
  height: 60px;
  transition: all .1s;
  display: grid;
  place-items: center;
}

.navIcon:hover{
  cursor: pointer;
  transform: scale(1.15);
}

.appView{
  width: 600px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 10px;
}

//SCROLLBAR

::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 5px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(32, 32, 32); 
  border-radius: 5px;
  height: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(0, 236, 225); 
}

@media (max-width: 720px) {
  .navIcon{
    width: 30px;
    height: 30px;
  }
  .appView{
    width: 100vw;
  }
}


@media
only screen and (-webkit-min-device-pixel-ratio: 2)      and (max-width: 700px),
only screen and (   min--moz-device-pixel-ratio: 2)      and (max-width: 700px),
only screen and (     -o-min-device-pixel-ratio: 2/1)    and (max-width: 700px),
only screen and (        min-device-pixel-ratio: 2)      and (max-width: 700px),
only screen and (                min-resolution: 192dpi) and (max-width: 700px),
only screen and (                min-resolution: 2dppx)  and (max-width: 700px) { 

  /* Medium screen, retina, stuff to override above media query */
  //dvh dynamic viewport unit for IOS devices
  body{
    height: 100dvh;
  }

  .app{
    height: 100dvh;
  }

 .navIcon{
    width: 30px;
    height: 30px;
  }
  .appView{
    width: 100vw;
  }
}